'use client';

import { useState } from 'react';
import Game from './Game'; // Import Game.tsx frá sama möppu

const Home = () => {
  const [username, setUsername] = useState('');
  const [gameStarted, setGameStarted] = useState(false);

  // Þegar notandi ýtir á takkann, setjum við gameStarted á true ef notendanafnið er ekki tómt.
  const handleStartGame = (e: React.FormEvent) => {
    e.preventDefault();
    if (username.trim()) {
      setGameStarted(true);
    }
  };

  return (
    <div style={{ textAlign: 'center', marginTop: '50px' }}>
      <h1>Fótbolta leikurinn!!!</h1>
      {/* Ef leikurinn er ekki hafinn, birtist formið */}
      {!gameStarted ? (
        <form onSubmit={handleStartGame} style={{ marginTop: '20px' }}>
          <label style={{ fontSize: '1.2rem' }}>
            Notendanafn:
            <input
              type="text"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              placeholder="Sláðu inn notendanafn"
              style={{
                marginLeft: '10px',
                padding: '8px 12px',
                fontSize: '1rem',
                borderRadius: '5px',
                border: '1px solid #ccc'
              }}
            />
          </label>
          <button
            type="submit"
            style={{
              padding: '10px 20px',
              fontSize: '1.2rem',
              marginLeft: '10px',
              border: 'none',
              borderRadius: '5px',
              cursor: 'pointer',
              backgroundColor: '#28a745',
              color: '#fff',
              transition: 'background-color 0.3s'
            }}
            onMouseOver={(e) =>
              ((e.target as HTMLButtonElement).style.backgroundColor = '#218838')
            }
            onMouseOut={(e) =>
              ((e.target as HTMLButtonElement).style.backgroundColor = '#28a745')
            }
          >
            Byrja leik
          </button>
        </form>
      ) : (
        // Þegar leikurinn er hafinn, birtist Game.tsx
        <Game username={username} />
      )}
    </div>
  );
};

export default Home;
